body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{
  padding:20px 70px;
  background-color: black;
  color:azure;
  font-weight: bold;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  letter-spacing: 5px;
  display:flex;
  justify-content: space-between;
}
.cart-icon .counter{
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position:relative;
  right:7px;
  top:-6px;
}
.cart-icon{
  display: flex;
}
.icon{
  font-size: 30px;
}
img{
  height: 150px;
}
.card-wrapper{
  display:inline-flex;
  width:320px;
  border-radius: 10px;
  box-shadow: 0px 0px 7px black;
  gap:10px;
}
.image-wrapper{
  width:130px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid rgb(143, 143, 143);
}
.content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap:10px;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color:white;
  border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 5px;
  border:1px solid grey;
}
.quantity div{
  height:22px;
  width:22px;
  display:flex;
  align-items: center;
  justify-content: center;
}
.quantity>div:first-child, .quantity>button{ 
  background-color: rgb(117, 15, 70) !important;
  color:white;
  border-radius: 0;
}
.quantity>button:disabled{
  opacity:0.7;
}
main{
  padding:20px;
  display: flex;
  flex-wrap: wrap;
  min-height:80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}
.total{
  font-weight: bold;
  bottom:0;
  width:100%;
  padding-right:30px;
  text-align:right;
  padding:18px;
  position:fixed;
  box-sizing: border-box;
  background-color: rgb(162, 156, 156);
}
